@import url("https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Anuphan:wght@300&family=Sedgwick+Ave+Display&display=swap");

@import "./styles/animate.min.css";

:root {
  --radial: 132deg;
  --gradColor_1: rgba(55, 163, 207, 0.35);
  --gradColor_2: rgba(34, 186, 214, 0.35);
  --gradColor_3: rgba(34, 186, 214, 1);
  --gradColor_gray_1: rgba(255, 255, 255, 1);
  --gradColor_gray_2: rgba(255, 90, 214, 1);
  --gradColor_gray_3: rgba(255, 90, 157, 1);
  --gradColorgray_2: rgba(155, 90, 157, 1);
  --linerGradient: var(--gradColorgray_2) 15%, var(--gradColor_3) 90%;
  --linerGradient_2: var(--gradColor_1) 15%, var(--gradColor_2) 90%;
  --linerGradient_3: var(--gradColor_3) 15%, var(--gradColorgray_2) 90%;
  --animationSpeed: 3s;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-size: cover;
  background-color: black;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  width: 100vw;
  height: 100vh;
}

html {
  overflow-x: hidden !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#background-image canvas {
  height: 100vh !important;
  width: 100vw !important;
  background-color: transparent;
  margin: 0;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .iconSm {
    @apply text-gradB bg-gradO transition-all cursor-pointer w-[2.5rem] h-[2.5rem] hover:drop-shadow-lg hover:bg-gradB hover:text-gradO z-10;
  }
  .iconSmInvert {
    @apply text-gradO bg-gradB transition-all cursor-pointer w-[2.5rem] h-[2.5rem] hover:drop-shadow-lg hover:bg-gradO hover:text-gradB z-10;
  }
  .iconLg {
    @apply text-gradB bg-gradO transition-all cursor-pointer w-[5rem] h-[5rem] hover:drop-shadow-lg hover:bg-gradB hover:text-gradO z-10;
  }
  .iconLgInvert {
    @apply text-gradO bg-gradB transition-all cursor-pointer w-[5rem] h-[5rem] hover:drop-shadow-lg hover:bg-gradO hover:text-gradB z-10;
  }
  .portraitRight {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute right-full w-screen h-screen;
  }
  .portraitLeft {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute left-0;
  }
  .portraitI {
    @apply bg-white max-w-[69vw] max-h-[36vh] sm:max-w-[640px] sm:max-h-[900px] absolute w-screen h-screen;
  }

  .Alfa {
    font-family: "Alfa Slab One", cursive;
  }

  .Anuphan {
    font-family: "Anuphan", sans-serif;
  }

  .Sedgwick {
    font-family: "Sedgwick Ave Display", cursive;
  }
  .grad-background {
    background: rgb(255, 255, 255);
    background: linear-gradient(var(--radial), var(--linerGradient_2));
  }
  .btn_glass_purple_parent {
    @apply rounded-lg flex-[45%] border-2 border-black/5 border-solid Alfa text-center p-10 relative overflow-hidden;
  }
  .btn_glass_purple {
    @apply hover:animate-pulse backdrop-blur-xl absolute w-full h-full top-0 left-0 inline-flex items-center transition-all justify-center hover:text-2xl;
  }
  .broken_glass {
    @apply backdrop-blur-3xl transition-all inline-flex justify-center items-center w-full;
  }
  .broken_glass:after {
    @apply block absolute w-full rounded-lg transition-all Anuphan bg-gradB text-gradO moon;
  }
  .mobile_btn {
    @apply rounded-full flex text-amber-400 absolute w-full h-full items-center justify-center;
  }
  .mobile_btn_icon {
    @apply w-1/2 h-1/2;
  }
  .space_invaders_container {
    @apply absolute z-30 flex items-center justify-center flex-col backdrop-blur-sm p-5;
  }
  .glass_animation {
    animation: bounce 0.1s ease-in-out;
  }
  .moon {
    animation: fadeIn 1s ease-in-out;
  }
  .boom {
    animation: wobble 1s ease-in-out;
  }
  .plane {
    animation: fadeInLeftBig 4s ease-in-out;
  }
  .hand {
    animation: slideInLeft 2s ease-in-out;
  }
  .exiting,
  .entering,
  .exiting,
  .exited {
    transition: all 1s ease-in-out;
  }
  .entered,
  .entering {
    z-index: 0;
  }
  .exiting,
  .exited {
    z-index: -1;
  }
  .exiting {
    opacity: 0;
  }
  .entering {
    opacity: 1;
  }
  .exited {
    display: none;
    opacity: 0;
  }
  .portfolio-title {
    background: linear-gradient(var(--radial), var(--linerGradient_3));
    background-size: contain;
    background-position: bottom center;
    background-repeat: no-repeat;
    font-size: 8vh;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    writing-mode: vertical-lr;
    pointer-events: none;
    text-align: center;
    height: 100%;
    position: relative;
    z-index: 300;
    opacity: 0.5;
    font-family: "Alfa Slab One", cursive;
  }
  .portfolio-title-mobile {
    background: linear-gradient(var(--radial), var(--linerGradient_3));
    background-size: contain;
    background-position: bottom right;
    background-repeat: no-repeat;
    font-size: 7.5vw;
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    pointer-events: none;
    text-align: center;
    position: relative;
    z-index: 300;
    opacity: 0.5;
    font-family: "Alfa Slab One", cursive;
  }
  .gradient_purple_text {
    background: linear-gradient(var(--radial), var(--linerGradient_3));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .broken_glass:after {
    content: "coming soon";
    transform: rotate(-15deg);
  }
}
